$dark: #313131;
$light: #ffffff;
$black: #000000;
$background: #ffffff;
$background2: #f0f0f0;

* {
	font-size: 18px;
	padding: 0;
	margin: 0;
	border: none;
	box-sizing: border-box;
	outline: 0;
	list-style-type: none;
	text-decoration: none;
	font-family: 'Gotham Pro';
	color: $dark;

	&::-webkit-scrollbar {
		width: 5px;
		height: 8px;
		background-color: #000000;
	}

	&::-webkit-scrollbar-thumb {
		background-color: #ffffff;
		border-radius: 9em;
	}

	&::-webkit-scrollbar-thumb:hover {
		background-color: #ffffff50;
	}
}

[data-scrollbar] {
	height: 100vh;
	z-index: 1;
}

.OpenModal {
	position: fixed;
	height: 100vh;
	width: 100vw;
}

.ScrollNon {
	padding-right: 5px;
	overflow: hidden;
}

h2 {
	font-size: 3.5rem;
}

@media (max-width: 1350px) {
	* {
		font-size: 16px;
	}
}

@media (max-width: 1250px) {
	h2 {
		font-size: 4.2rem;
	}
}

@media (max-width: 767px) {
	h2 {
		font-size: 3rem;
	}
}

@media (max-width: 500px) {
	h2 {
		font-size: 2.2rem;
	}
}
